/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */

@import 'variables.scss';
@import 'typography.scss';
@import url('https://fonts.googleapis.com/css?family=Roboto|Roboto+Slab');

html, body {
  font-family: 'Roboto Slab', 'Helvetica', sans-serif;
  font-size: 15px;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}


nav {
  position: fixed;
  z-index: 99;
  background: $primary-color;
  display: flex;
  width: 100%;
  height: 64px;
  padding-bottom: 1rem;
  box-shadow: 0 2px 4px #242424;
  transition: 0.4s ease all;
  ul {
    text-align: center;
    list-style: none;
    width: 50%;
    padding: 1rem 0 0 0;
    display:block;
    li {
      display: inline-block;
      a {
        font-weight: 600;
        transition: 0.4s ease all;
        padding: 2rem;
        color: $off-white;
        text-shadow: 2px 2px 4px #000;
        &:hover {
          color: #000;
          text-shadow: 0 0 0 #000;
        }
      }
    }
  }
  &.transparent {
    background: transparent;
    box-shadow: none;
    &:hover {
      background: $primary-color;

    }
  }
}

body {
  overflow-x: hidden;
  grid-template-areas:
    "header"
    "main"
    "footer";
  background: $off-white;
}

main {
  grid-template: "main";
}

footer {
  position: relative;
  grid-template: "footer";
  background-color: $primary-color;
  padding: 20px;
  transition: 0.3s ease all;

  .contact {
    transition: 0.3s ease all;
    color: #f4f4f4;
    text-align: center;
    flex-direction: column;
    form {
      display: flex;
      flex-wrap: wrap;

      .input-area {
        width: 45%;
        padding: 0 2rem;
        text-align: left;
        button {
          margin: 1rem;
          text-decoration: none;
          color: #fff;
          background-color: $secondary-color;
          text-align: center;
          letter-spacing: 0.5px;
          transition: 0.5s ease-out all;
          cursor: pointer;
          border: none;
          border-radius: 2px;
          display: inline-block;
          height: 36px;
          line-height: 36px;
          outline: 0;
          padding: 0 2rem;
          text-transform: uppercase;
          vertical-align: middle;
          -webkit-tap-highlight-color: transparent;
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);

          &:hover {
            background-color: adjust-hue($secondary-color, 15);
          }
        }
      }
      .input-group {
        position: relative;
        padding-top: 2rem;
        overflow: hidden;
        input,
        textarea {
          background: transparent;
          border: none;
          border-bottom: 1px solid $faded;
          width: 100%;
          padding: 1rem;
          color: $off-white;
          transition: 0.5s ease all;
          &:focus {
            border: none;
            border-bottom: 1px solid $secondary-color;
            box-shadow: 0 1px 0 0 $secondary-color;
            outline: none;
            + label {
              color: $secondary-color;
              top: 1rem;
            }
          }
          &:not(:placeholder-shown) {
            + label {
              top: 1rem;
            }

            &:valid {
              border-bottom: 2px solid $success-color;
              &:focus + label {
                color: $success-color;
              }
            }

            &:invalid {
              border-bottom: 2px solid $error-color;
              &:focus + label {
                color: $error-color;
              }
            }
          }
        }
        textarea {
          height: 173px;
        }
        label {
          transition: 0.5s ease all;
          color: $faded;
          position: absolute;
          left: 1rem;
          top: 3rem;
        }
        &.full-height {
          height: 100%;
          textarea {
            height: 100%;
          }
        }
      }
    }
  }
}

.loading {
  cursor: not-allowed;
  background-color: #858f93;
  .contact {
    pointer-events: none;
    opacity: 0.6;
  }

  .progress {
    opacity: 1;
  }
}

.progress {
  opacity: 0;
  display: block;
  position: absolute;
  top: -7px;
  left: 0;
  height: 7px;
  width: 100%;
  background-color: $secondary-color;
  border-radius: 2px;
  background-clip: padding-box;
  margin: 0.5rem 0 1rem 0;
  overflow: hidden; }
.progress .indeterminate {
  background-color: #9e9e9e; }
.progress .indeterminate:before {
  content: '';
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  -webkit-animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
  animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite; }
.progress .indeterminate:after {
  content: '';
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  transition: 0.3s ease all;
  will-change: left, right;
  -webkit-animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
  animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
  -webkit-animation-delay: 1.15s;
  animation-delay: 1.15s; }

@-webkit-keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%; }
  60% {
    left: 100%;
    right: -90%; }
  100% {
    left: 100%;
    right: -90%; } }
@keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%; }
  60% {
    left: 100%;
    right: -90%; }
  100% {
    left: 100%;
    right: -90%; } }
@-webkit-keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%; }
  60% {
    left: 107%;
    right: -8%; }
  100% {
    left: 107%;
    right: -8%; } }
@keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%; }
  60% {
    left: 107%;
    right: -8%; }
  100% {
    left: 107%;
    right: -8%; } }

.monster-numbers {
  background-color: forestgreen;
}
.monzo {
  background-color: #14233c;
}
.stunningpr {
  background-color: #263238;
}
.gw2 {
  background-color: rebeccapurple;
}
.lion-king {
  background-color: darkorange;
}
.emuas {
  background-color: #d50000;
}

.email-alert.show {
  opacity: 1;
}

.email-alert {
  opacity: 0;
  position: fixed;
  width: 100%;
  top: 79px;
  left: 0;
  height: 80px;
  text-align: center;
  z-index: 999;
  transition: 0.3s ease all;
  p {
    color: #FAFAFA;
    margin: 1rem;
    font-weight: bold;
    font-size: 2rem;
  }
}

.email-alert.error {
  background: $error-color;
}

.email-alert.success {
  background: $success-color;
}


@media #{$medium-and-down} {

  nav {
    ul {
      padding-left: 2rem;
      display: flex;
      li {
        width: 33%;
        a {
          padding: 0 1rem;
        }
      }
      &:first-child {
        width: 60%;
      }
      &:last-child {
        flex-direction: column;
        align-items: center;
        width: 40%;
      }
    }

  }

  footer {
    .contact {
      form {
        .input-area {
          width: 100%;
        }
      }
    }
  }

}

@media #{$small-and-down} {

  nav {
    ul {
      padding-left: 2rem;
      display: flex;
      li {
        text-align: center;
        width: 33%;
        a {
          padding: 0 1rem;
        }
      }
      &:first-child {
        max-width: 70%;
      }
      &:last-child {
        flex-direction: row;
        align-items: center;
        max-width: 30%;
        li {
          width: 100%;
        }
      }
    }

  }
}
